import popupLayer from '@/components/search-select/popup-layer.vue';
import slFilterView from '@/components/search-select/filter-view.vue';
import Constant from '../../util/constant.js';
export default {
	components: {
		popupLayer,
		slFilterView
	},
	props: {
		//所有数据展示
		menuList: {
			type: Array
		},
		//选中数据
		selectList:{
			type: Object
		},
		themeColor: {
			type: String,
			default: "#000000"
		},
		color: {
			type: String,
			default:'rgba(0,0,0,0.90)'
		},
		independence: {
			type: Boolean,
			default: false
		},
		isTransNav: {
			type: Boolean,
			default: false
		},
		navHeight: {
			type: Number,
			default: 0
		},
		topFixed: {
			type: Boolean,
			default: false
		},
		closeSelect:{
			type:Boolean,
			default: false
		}
	},
	computed:{
		computedClose(){
			if(this.closeSelect){
				this.$refs.popupRef.close();
			}
		}
	},
	onReady() {
		
	},

	data() {
		return {
			showPopup:false,
			tabHeight: 50,
			subKey:Constant.headKey
		}
	},
	
	methods: {
		/**
		 * 菜单打开显示与否
		 */
		showMenu(){
			this.showPopup = !this.showPopup;
			if(this.showPopup){
				this.$refs.popupRef.show();
			}else{
				this.$refs.popupRef.close();
			}
			this.$emit("selectPopup",true);	
		},
		
		/**
		 * 返回选中的结果数据
		 * @param {Object} item
		 */
		confirmHeadItem(item) {
			this.close();
			this.$emit("headResult",item);

		},
		
		/**
		 * 关闭弹出框
		 */
		close() {
			this.showPopup = false;
			this.$refs.popupRef.close();
		}
	
	}
}
